h3 {
  position: relative;
  display: block;
}
h3::after {
  content: "";
  position: absolute;
  height: 2px;
  width: 43%;
  background-color: black;
  top: 100%;
  right: 0;
}
h3::before {
  content: "";
  position: absolute;
  height: 2px;
  width: 43%;
  background-color: black;
  top: 100%;
  left: 0;
}

